@import url(https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,500;0,600;1,500&display=swap);
*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html { overflow-x: hidden !important; }

body {
  font-family: 'Space Grotesk', sans-serif;
  width: 100vw;
  background-color: #fff;
  overflow-x: hidden !important;
}

#monLogo { width: 40px;} /* LOGO RLPZ */


/* SECTION ACCUEIL */
.remiLopez {
  font-size: 10vw;
   letter-spacing: 0.2rem;
   color: red;
   -webkit-text-stroke-width: 1px;
   -webkit-text-stroke-color: red;
   margin-bottom: 20px;
   text-shadow: 2px 2px 1px #464646;
}
.portrait {
  margin-top: 50px;
  width: 30vw;
  -webkit-animation: float 5s ease-in-out infinite;
          animation: float 5s ease-in-out infinite;
}

@-webkit-keyframes float {
  0% {
    -webkit-transform: translatey(15px);
            transform: translatey(15px);
  }
  50% {
    -webkit-transform: translatey(25px);
            transform: translatey(25px);
  }
  100% {
    -webkit-transform: translatey(15px);
            transform: translatey(15px);
  }
}

@keyframes float {
  0% {
    -webkit-transform: translatey(15px);
            transform: translatey(15px);
  }
  50% {
    -webkit-transform: translatey(25px);
            transform: translatey(25px);
  }
  100% {
    -webkit-transform: translatey(15px);
            transform: translatey(15px);
  }
}

@media (min-width: 768px) {
  .remiLopez  { font-size: 8vw; }
  .portrait {
    margin-top: 0px;
    max-width: 300px;
    margin-left: 5vw;
  }
} /* FIN **** SECTION ACCUEIL */


/* SECTION A PROPOS */
.aboutme {
  width: 90vw;
  margin: 0 auto 80px auto;
  padding-top: 100px;
}

.aboutme h2 {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 20px;
  text-transform: uppercase;
  color: red;
  font-weight: 300;
  margin: 0 0 30px 0;
  letter-spacing: 0.25rem;
  border-bottom: 3px solid red;
}

.aboutme p {
  letter-spacing: 0.05rem;
  line-height: 1.5rem;
  text-align: justify;
  margin: 30px 0;
}

@media (min-width: 768px) {
  .aboutme {
    background-image: url(/static/media/illu-aboutme.4f276e43.png);
    background-repeat: no-repeat;
    background-size: 180px;
    background-position: bottom left 5%;
  }
  .aboutme h2, .aboutme p { margin-left: 35%; }
}
/* FIN **** SECTION A PROPOS */

/* SECTION COMPETENCES */
#competences {
  height: auto;
  margin-bottom: 180px;
  padding-top: 140px;
}

#competences h2 {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto 50px auto;
  font-size: 20px;
  text-transform: uppercase;
  color: red;
  font-weight: 300;
  letter-spacing: 0.25rem;
  border-bottom: 3px solid red;
}

#text {
  width: 100vw;
  padding: 100px 0;
  white-space: nowrap;
  overflow-x: hidden !important;
  background-image: url(/static/media/illu-competences.1fadcab9.png);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
}

#text .line1, #text .line2, #text .line3 {
  display: flex;
  -webkit-animation: infiniteText 10s linear infinite;
          animation: infiniteText 10s linear infinite;
  cursor: pointer;
  -webkit-animation-play-state: running;
          animation-play-state: running;
  margin-bottom: 20px;
}

#text .line1 span, #text .line2 span, #text .line3 span {
  font-size: 30px;
  font-weight: 700;
  text-transform: uppercase;
  color: black;
  margin: 0 65px 0;
  transition: all ease 0.5s;
}

#text .line2 { animation-direction: reverse; }

#text .line2 span { margin: 0 50px 0; }

#text .line3 span { margin: 0 50px 0; }

#text .line:hover {
  -webkit-animation-play-state: paused;
          animation-play-state: paused;
}

#text span:nth-child(odd), #text span:nth-child(even):hover {
  color: transparent;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
}

#text span:nth-child(odd):hover {
  color: black;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
}

@media (min-width: 768px) {
  #text .line1 span, #text .line2 span, #text .line3 span {
    font-size: 50px;
  }
  #text .line2 span {
    margin: 0 90px 0;
  }
}

@-webkit-keyframes infiniteText {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}

@keyframes infiniteText {
  0% {
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  100% {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
}
/* FIN **** SECTION COMPETENCES */

/* SECTION PORTFOLIO */
#portfolio {
  width: 100vw;
  height: auto;
  padding: 10vh 0 0 0;
}

#portfolio-wrapper {
  width: 100%;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 50% 50%;
      grid-template-columns: 50% 50%;
}

#ProjetUn, #ProjetQuatre, #ProjetCinq {
  background-color: black;
  color: white;
}

#ProjetDeux, #ProjetTrois, #ProjetSix {
  background-color: red;
  color: black;
}

#ProjetUn:hover, #ProjetDeux:hover, #ProjetTrois:hover, #ProjetQuatre:hover, #ProjetCinq:hover, #ProjetSix:hover {
  background-color: transparent;
  transition: all ease 0.5s;
}

#ProjetUn:hover {
  background-image: url(/static/media/Calculator.9c0cc65d.png);
  background-size: cover;
  background-position: center center;
  transition: all ease-in-out 0.5s;
}

#ProjetDeux:hover {
  background-image: url(/static/media/Sunnyside.5d077fc1.png);
  background-size: cover;
  background-position: center center;
  transition: all ease-in-out 0.5s;
}

#ProjetTrois:hover {
  background-image: url(/static/media/loopstudio.d9178033.jpg);
  background-size: cover;
  background-position: center center;
  transition: all ease-in-out 0.5s;
}

#ProjetQuatre:hover {
  background-image: url(/static/media/Room-Page.d377dee6.png);
  background-size: cover;
  background-position: center top;
  transition: all ease-in-out 0.5s;
}

#ProjetCinq:hover {
  background-image: url(/static/media/crowdfunding.74b4ddf2.jpg);
  background-size: cover;
  background-position: center center;
  transition: all ease-in-out 0.5s;
}

#ProjetSix:hover {
  background-image: url(/static/media/Pageview.7d38199a.png);
  background-size: cover;
  background-position: center center;
  transition: all ease-in-out 0.5s;
}

#Github {
  text-align: center;
  margin-top: 40px;
  z-index: 4;
}

#github {
  -webkit-text-decoration: transparent;
          text-decoration: transparent;
  width: -webkit-max-content;
  width: max-content;
  padding: 15px 25px;
  font-family: 'Space Grotesk', sans-serif;
  text-transform: uppercase;
  background-color: red;
  border: transparent;
  box-shadow: 2px 2px 10px 2px #7a7a7a;
  color: white;
}

#github:hover {
  background-color: black;
  transition: ease-in-out 0.3s;
}

@media (min-width: 768px) {
  #ProjetQuatre {
    background-color: red;
    color: black;
    transition: all ease 0.5s;
  }
  #ProjetTrois {
    background-color: black;
    color: white;
    transition: all ease 0.5s;
  }
} /* FIN **** SECTION PORTFOLIO */

/* SECTION CONTACT */
#contact {
  width: 100%;
  padding: 18vh 5% 0 5%;
  height: auto;
  margin: 0 auto;
}

#contact-img { text-align: center; }

#contactImg img {
  width: 80%;
  -webkit-animation: plane 2s ease infinite;
          animation: plane 2s ease infinite;
}

#contact h2 {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  font-size: 20px;
  text-transform: uppercase;
  color: red;
  font-weight: 300;
  margin: 40px 0 30px 0;
  letter-spacing: 0.25rem;
  border-bottom: 3px solid red;
}

#contact p {
  letter-spacing: 0.10rem;
  line-height: 1.6rem;
  text-align: justify;
  margin-bottom: 50px;
}

form input {
  outline: none;
  width: 100%;
  padding-left: 30px;
  height: 3rem;
  border: 1px solid lightgray;
  background: transparent;
  margin: 0 auto 20px auto;
  box-shadow: 2px 2px 10px 2px #7a7a7a;
}

#name::-webkit-input-placeholder, #email::-webkit-input-placeholder {
  font-family: 'Space Grotesk', sans-serif;
}
#name:-ms-input-placeholder, #email:-ms-input-placeholder {
  font-family: 'Space Grotesk', sans-serif;
}
#name::placeholder, #email::placeholder {
  font-family: 'Space Grotesk', sans-serif;
}

/* .error-icon {
  width: 100%;
  background-repeat: no-repeat;
  background-position: 97% 49%;
  border: 2px solid red !important;
} */
#errorMsg {
  color: red;
  margin: -10px 5% 15px 0;
  font-size: 11px;
  float: right;
}

#btnForm {
  height: 2.5rem;
  width: 170px;
  font-family: 'Space Grotesk', sans-serif;
  text-transform: uppercase;
  background-color: black;
  color: white;
  border: transparent;
  box-shadow: 2px 2px 10px 2px #7a7a7a;
}

#btnForm:hover {
  background-color: red;
  transition: ease-in-out 0.3s;
  -webkit-transform: scaleZ(20%);
          transform: scaleZ(20%);
}

@-webkit-keyframes plane {
  0% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
  50% {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
  }
  100% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
}

@keyframes plane {
  0% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
  50% {
    -webkit-transform: translateX(5px);
            transform: translateX(5px);
  }
  100% {
    -webkit-transform: translateX(-5px);
            transform: translateX(-5px);
  }
}

@media (min-width: 768px) {
  #contact {
    display: flex;
    flex-direction: row-reverse;
  }
  #contactImg {
    text-align: right;
    -ms-grid-row-align: center;
    align-self: center;
  }
  #contactImg img { width: 70%; }
} /* FIN **** SECTION CONTACT */

/* SECTION FOOTER */
footer img {
  width: 22px;
  margin-right: 10px;
}

footer a {
  font-size: 13px;
  -webkit-text-decoration: dotted;
          text-decoration: dotted;
  color: white;
}
footer a:hover {
  color: red;
  border-bottom: 0.125em dotted red;
} /* FIN **** SECTION FOOTER */
